// https://developer.mozilla.org/en-US/docs/Web/Guide/HTML/Editable_content

<template>
	<div
		ref="editorPanel"
		@mouseover="refreshToolbar()"
		class="toolbar bg-white rounded-md shadow-md border-gray-400 border-2 flex items-center"
	>
		<div v-for="(tool, index) in toolbar" :key="index" class="h-8">
			<div v-if="tool.type == 'divider'" class="w-0.5 py-1 h-full bg-gray-300"></div>

			<button v-else class="w-8 h-full hover:opacity-60" @click="formatSelection(tool.cmd)">
				<font-awesome-icon :icon="tool.icon" />
			</button>
		</div>
	</div>
</template>

<script>
export default {
	name: "EditorToolBar",
	data() {
		return {
			hideTimeout: null,
			toolbar: [
				{
					icon: "bold",
					cmd: "bold",
				},
				{
					icon: "italic",
					cmd: "italic",
				},
				{
					icon: "underline",
					cmd: "underline",
				},
				{
					icon: "eraser",
					cmd: "removeFormat",
				},
				{
					type: "divider",
				},
				{
					icon: "link",
					cmd: "createLink",
				},
			],
		};
	},
	methods: {
		onTextSelection() {
			const selection = window.getSelection();
			const tb = this.$refs.editorPanel;

			clearTimeout(this.hideTimeout);
			if (selection == null || selection.type != "Range") {
				tb.style.opacity = 0;
				return;
			}

			const bounding = selection.getRangeAt(0).getBoundingClientRect();
			const relative = document.body.parentNode.getBoundingClientRect();
			const tbWidth = (this.toolbar.length * 32) / 2;

			tb.style.opacity = 1;
			tb.style.top = bounding.bottom - relative.top - bounding.height - 40 + "px";
			tb.style.right = relative.right - bounding.right + bounding.width / 2 - tbWidth + "px";

			this.hideTimeout = setTimeout(() => {
				tb.style.opacity = 0;
			}, 2000);
		},
		refreshToolbar() {
			const tb = this.$refs.editorPanel;

			tb.style.opacity = 1;
			clearTimeout(this.hideTimeout);

			this.hideTimeout = setTimeout(() => {
				tb.style.opacity = 0;
			}, 1000);
		},
		formatSelection(sCmd, sValue) {
			switch (sCmd) {
				case "createLink":
					sValue = prompt("Enter a URL:", "http://");

					if (!sValue || sValue == "" || sValue == "http://") {
						alert("Invalid URL");
						return;
					}

					var selection = document.getSelection();
					document.execCommand(sCmd, true, sValue);
					selection.anchorNode.parentElement.target = "_blank";

					return;
			}

			document.execCommand(sCmd, false, sValue);
		},
	},
};
</script>

<style>
.toolbar {
	@apply absolute z-10 opacity-0 transition-opacity duration-500;
	box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.6);
}

.bb {
	border: 2px dashed rgba(255, 255, 255, 0.05);
	transition: border 300ms;
}

.bb:hover {
	border-color: rgba(255, 255, 255, 0.2);
}

.bb:focus {
	border-color: rgba(255, 255, 255, 0.7);
	outline: none;
}

*:not(:is(:hover, :focus, :focus-within)) > .hidden-outside {
	display: none;
}
</style>
