<template>
	<section class="mb-12" :class="{ bb: editing }">
		<Subheading v-if="block.title" v-model="block.title" class="mb-12" :editable="editing" />

		<segment
			v-for="(data, index) in block.segments"
			ref="segment"
			:key="index"
			:data="data"
			:style="{ flex: (data.size ? data.size : 50) + '%' }"
			:editing="editing"
			@action="onAction(index, $event)"
			class="mb-4"
		/>

		<segment v-if="editing" @reload="onReload($event)" />
	</section>
</template>

<script>
import Subheading from "@/components/Subheading";

export default {
	name: "SectionBlock",
	props: ["block", "editing"],
	components: {
		Subheading,
	},
	methods: {
		onReload(newData) {
			this.block.segments.push(newData);

			this.$forceUpdate();
			this.recursiveUpdate();
		},
		onAction(index, newData) {
			if (!newData) {
				this.block.segments.splice(index, 1);
			} else {
				this.block.segments[index] = newData;
			}

			this.recursiveUpdate();
		},
		recursiveUpdate() {
			this.$children.slice(1, this.$children.length - 1).forEach(segment => {
				segment.onUpdate();
			});

			this.$forceUpdate();
		},
	},
};
</script>
