<template>
	<div class="projects">
		<Menu />
		<ArticleMatrix v-if="this.projects[this.slug]" :editing="isEditing" :project="this.projects[this.slug]" />
		<Footer />
	</div>
</template>

<script>
import Menu from "@/components/Menu";
import ArticleMatrix from "@/components/ArticleMatrix";
import Footer from "@/components/Footer";

import ProjectsJSON from "@/assets/data/projects.json";

export default {
	name: "Projects",

	components: {
		Menu,
		ArticleMatrix,
		Footer,
	},

	data() {
		return {
			projects: ProjectsJSON,
			slug: null,
			isCtrl: false,
		};
	},

	computed: {
		isEditing() {
			return new URLSearchParams(window.location.search).get("editing") === "true";
		},
	},

	methods: {
		save() {
			const blob = new Blob([JSON.stringify(this.projects)], {
				type: "text/plain",
			});
			const e = document.createEvent("MouseEvents"),
				a = document.createElement("a");

			a.download = "projects.json";
			a.href = window.URL.createObjectURL(blob);
			a.dataset.downloadurl = ["text/json", a.download, a.href].join(":");
			e.initEvent("click", true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
			a.dispatchEvent(e);
		},
	},

	mounted() {
		this.slug = this.$route.params.slug;

		if (typeof this.projects[this.slug] === "undefined") {
			this.$router.push("/");
			return;
		}

		document.title = "Jeremy Webster's Portfolio - " + this.projects[this.slug].title;

		document.onkeyup = function (e) {
			if (e.keyCode == 17) this.isCtrl = false;
		};

		let instance = this;
		document.onkeydown = function (e) {
			if (e.keyCode == 17) this.isCtrl = true;
			if (e.keyCode == 83 && this.isCtrl == true) {
				instance.save();

				return false;
			}
		};
	},
};
</script>
