<template>
	<div class="pt-28 sm:pt-40 px-6 md:px-16 mb-12">
		<div class="max-w-5xl mx-auto">
			<div @mouseup="editing ? $refs.editorToolBar.onTextSelection() : null">
				<SectionBlock v-for="(block, index) in project.blocks" :key="index" :block="block" :editing="editing" />
			</div>

			<button
				v-if="editing"
				@click="addSection"
				class="h-52 w-full bb flex justify-center items-center font-bold"
				style="color: rgba(255, 255, 255, 0.1); font-size: 8em"
			>
				<div class="-mt-10">+</div>
			</button>

			<EditorToolBar v-if="editing" ref="editorToolBar" />
		</div>
	</div>
</template>

<script>
import SectionBlock from "@/components/SectionBlock";
import EditorToolBar from "@/components/EditorToolBar";

export default {
	name: "ArticleMatrix",
	props: ["project", "editing"],
	components: {
		SectionBlock,
		EditorToolBar,
	},
	methods: {
		addSection() {
			if (!this.project.blocks || this.project.blocks == null) this.project.blocks = [];

			this.project.blocks.push({
				title: "New Section",
				segments: [],
			});

			this.$forceUpdate();
		},
	},
};
</script>
